.hero-container{width:100%;height:100vh}.hero-image{position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover;object-position:top;opacity:1}.hero video{position:absolute;top:0;left:0;width:100%;height:100%;object-fit:cover;object-position:top;opacity:1}.hero .overlay{position:absolute;top:0;left:0;width:100%;height:100%;background:#2c272e;mix-blend-mode:overlay}.hero .text{color:white;position:relative;z-index:10}.hero .text h2{font-size:5em;font-weight:800;line-height:1em;text-transform:uppercase}.hero .text h3{font-size:4em;font-weight:700;line-height:1em;text-transform:uppercase}.hero .text h3 span{font-size:1em;font-weight:700;line-height:1em;text-transform:uppercase;color:#ffac41;mix-blend-mode:difference}.hero .text p{font-size:1.1em;margin:20px 0;font-weight:400;max-width:700px}.hero .btn{background-color:#ffac41;font-size:1rem;text-transform:uppercase;border:none}

