.product-details{height:100vh;width:100%}.product-details .quantity .btn-dark{height:42px;width:42px}.product-details .quantity p{font-size:2rem;text-align:center;min-width:7rem}.product-details .price p{font-size:1.5rem;font-weight:800}@media (min-width: 992px){.product-details{background-image:url(/static/media/product_details_bg.79b6a03c.jpg);background-size:cover;background-position:center}.product-details-container{position:relative;background:inherit;border-radius:1rem;z-index:1}.product-details-container:hover::before{opacity:0.9}.product-details-container:hover::after{opacity:0.7}.product-details-container::before{content:"";position:absolute;width:100%;height:100%;top:0;left:0;border-radius:1rem;z-index:-3;background:inherit;filter:blur(20px);opacity:0.7;transition:all 250ms ease-in}.product-details-container::after{content:"";position:absolute;width:100%;height:100%;top:0;left:0;border-radius:1rem;z-index:-1;background:#ddd;opacity:0.5;transition:all 250ms ease-in}}

